.theme-dark {
    .case_study--branding {
        .case_study__hero {
            background-image: url(../img/bg-curves--wo.svg);
        }
    }
    .case_study--production {
        .case_study__hero {
            background-image: url(../img/bg-herringbone--wo.svg);
        }
    }
    .case_study__process {
        color: var(--col-red-400);
    }
    .caseStudies {
        .case_study__hero {
            background-image: url(../img/bg-topography--wo.svg);
        }
    }
}
.theme-light {
    .case_study--branding {
        .case_study__hero {
            background-image: url(../img/bg-curves--dark.svg);
        }
    }
    .case_study--production {
        .case_study__hero {
            background-image: url(../img/bg-herringbone--dark.svg);
        }
    }
    .case_study__process {
        color: var(--col-green-400);
    }
    .caseStudies {
        .case_study__hero {
            background-image: url(../img/bg-topography--dark.svg);
            margin-bottom: 3rem;
        }
    }
}

.case_study {
    &__hero {
        min-height: 480px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;
        isolation: isolate;
        &::before {
            content: "";
            position: absolute;
            inset: 0;
            background-color: hsl(var(--hsl-dark-400) / 0.8);
            z-index: -1;
        }

        display: grid;
        place-content: center;
        text-align: center;
        margin-bottom: 5rem;
    }
    &__process {
        font-size: var(--fs-500);
    }


    h2 {
        font-weight: var(--fw-400);
        max-width: 700px;
        margin-inline: auto;
        text-wrap: balance;
        line-height: 1.3;
    }

    h3 {
        font-size: var(--fs-400);
        font-weight: var(--fw-700);
        margin-bottom: 0;
    }

    &__nav {
        position: absolute;
        top: 1rem;
        right: 1rem;
        font-size: var(--fs-300);
        z-index: 999;
        span {
            padding-inline: 0.25rem;
            color: var(--col-red-400);
            font-size: 1rem;
        }
    }
    &__footer-nav {
        display: flex;
        justify-content: space-around;
        text-align: center;
        font-size: var(--fs-300);
        z-index: 999;
        width: min(50%,90vw);
        margin-inline: auto;
    }

}

.theme-light .case_study__hero::before {
    background-color: hsl(var(--hsl-light-400) / 0.8);
}

.caseStudies {
    .case_study__hero {
        margin-bottom: 2rem;
    }

    .btn-red:not(.active) {
        background-color: transparent;
        border: 1px solid var(--col-red-400);
    }
}


:root {
    --_content_bg: hsl(0 0% 0% / 0.9);
    --_title: var(--col-light-400);
}
.Light {
    --_content_bg: hsl(0 0% 100% / 0.9);
    --_title: var(--col-dark-400);
}

#csgrid {
    .caseStudy {
        padding-inline: 0;
        // padding-inline: 0.25rem;
        &_card {
            display: block;
            width: 100%;
            // outline: 1px solid white;
            // margin-bottom: 0.5rem;
            position: relative;
            &__image {
                aspect-ratio: 1;
                width: 100%;
                object-fit: cover;
                object-position: center;
            }
            &__content {
                opacity: 0;
                padding: 0.5rem;
                position: absolute;
                inset: 0;
                // background-color: hsl(0 0% 0% / 0.9);
                background-color: var(--_content_bg);
                display: grid;
                place-content: center;
                text-align: center;
                transition: opacity var(--transition);
                .article-title {
                    color: var(--_title);
                }
                .article-category {
                    font-size: var(--fs-300);
                    color: var(--col-red-400);
                }
            }
            &:hover {
                .caseStudy_card__content {
                    opacity: 1;
                }
            }
        }
    }
}

